import Header from '../components/Header';
import Link from 'next/link';

const PostLink = props => (
    <li>
        {/* <Link href={`/post?title=${props.title}`}> */}
        <Link href="/p/[id]" as={`/p/${props.id}`}>
            <a>{props.title}</a>
        </Link>
    </li>
);

export default function Index() {
    return (
        <div>
            <Header />
            <ul>
                <PostLink id="Hello Next.js" />
            </ul>
        </div>
    );
}